.list-overflow-container {
  max-height: 545px;
  border: none;
  border-radius: 3px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 3px;
  border-left: none;
  border-right: none;
  margin-bottom: 5px;
  border: lightgrey;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: rgba(10, 82, 176, 0.721);
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

